}

@mixin text-wariant($bg-color, $name) {
  a.bg-#{$name}:hover {
    background-color: darken($bg-color, 5%);
  }

  a.text-#{$name}:hover {
    color: darken($bg-color, 5%);
  }

  .text-#{$name} {
    color: $bg-color;
  }

  .text-#{$name}-lt {
    color: darken($bg-color, 5%);
  }

  .text-#{$name}-lter {
    color: darken($bg-color, 10%);
  }

  .text-#{$name}-dk {
    color: darken($bg-color, 5%);
  }

  .text-#{$name}-dker {
    color: darken($bg-color, 10%);
  }
}

@mixin hover {
  &:hover {
    @content;
  }
}

@mixin hover-focus {
  &:hover,
  &:focus {
    @content;
  }
}

@mixin hover-active {
  &:hover:active {
    @content;
  }
}

@mixin button-size(
  $padding-y,
  $padding-x,
  $font-size,
  $line-height,
  $border-radius,
  $height: auto,
  $iconWdith: px2rem(20px)
) {
  padding: $padding-y $padding-x;
  font-size: $font-size;
  line-height: $line-height;
  border-radius: $border-radius;
  height: $height;

  .#{$ns}Button-icon:first-child:not(:last-child):not(.pull-right),
  > .pull-left {
    margin-right: $padding-x;
  }

  .#{$ns}Button-icon:last-child:not(:first-child):not(.pull-left),
  > .pull-right {
    margin-left: $padding-x;
  }
}

@mixin button-variant(
  $background,
  $border: $background,
  $color: $white,
  $hover-background: darken($background, 7.5%),
  $hover-border: darken($border, 10%),
  $hover-color: $color,
  $active-background: darken($background, 10%),
  $active-border: darken($border, 12.5%),
  $active-color: $color
) {
  color: $color;
  background-color: $background;
  border-color: $border;
  box-shadow: $Button-boxShadow;

  @include hover-focus {
    color: $hover-color;
    background-color: $hover-background;
    border-color: $hover-border;
  }

  &.is-disabled,
  &:disabled {
    @if variable-exists(Button-onDisabled-bg) {
      background-color: $Button-onDisabled-bg;
    } @else {
      background-color: $background;
    }

    @if variable-exists(Button-onDisabled-color) {
      color: $Button-onDisabled-color;
    } @else {
      color: $color;
    }

    @if variable-exists(Button-onDisabled-borderColor) {
      border-color: $Button-onDisabled-borderColor;
    } @else {
      border-color: $border;
    }
  }

  &:not(:disabled):not(.is-disabled):active,
  &:not(:disabled):not(.is-disabled).is-active {
    color: $active-color;
    background-color: $active-background;
    border-color: $active-border;
  }
}
